import React, { useState, useEffect } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { AlertTriangle, CreditCard, Lock } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

const CreditGuard = ({ 
  children, 
  requiredCredits = 1, 
  feature = "this feature",
  showUpgradeButton = true 
}) => {
  const [balance, setBalance] = useState(null);
  const [loading, setLoading] = useState(true);
  const [hasAccess, setHasAccess] = useState(false);

  useEffect(() => {
    checkCreditBalance();
    
    // Listen for credit balance updates
    const handleCreditUpdate = () => {
      checkCreditBalance();
    };
    
    window.addEventListener('creditBalanceUpdate', handleCreditUpdate);
    
    return () => {
      window.removeEventListener('creditBalanceUpdate', handleCreditUpdate);
    };
  }, [requiredCredits]);

  const checkCreditBalance = async () => {
    try {
      setLoading(true);
      const response = await axiosInstance.get('/api/credits/balance');
      const balanceData = response.data.data;
      setBalance(balanceData);
      
      // Super admins have unlimited access
      if (balanceData.isSuperAdmin) {
        setHasAccess(true);
      } else {
        // Regular users need sufficient credits
        setHasAccess(parseFloat(balanceData.available_credits) >= requiredCredits);
      }
    } catch (error) {
      console.error('Error checking credit balance:', error);
      setHasAccess(false);
    } finally {
      setLoading(false);
    }
  };

  const handleUpgrade = () => {
    window.location.href = '/credits/purchase';
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!hasAccess) {
    return (
      <Card className="border-orange-200 bg-orange-50">
        <CardContent className="p-8 text-center">
          <div className="flex justify-center mb-4">
            <div className="p-3 bg-orange-100 rounded-full">
              <Lock className="h-8 w-8 text-orange-600" />
            </div>
          </div>
          
          <h3 className="text-lg font-semibold text-gray-900 mb-2">
            Insufficient Credits
          </h3>
          
          <p className="text-gray-600 mb-4">
            You need at least {requiredCredits} credit{requiredCredits > 1 ? 's' : ''} to use {feature}.
            {balance && !balance.isSuperAdmin && (
              <span className="block mt-1">
                Current balance: {balance.available_credits} credits
              </span>
            )}
          </p>

          {showUpgradeButton && (
            <div className="space-y-3">
              {/* <Button 
                onClick={handleUpgrade}
                className="bg-blue-600 hover:bg-blue-700 text-white"
              >
                <CreditCard className="h-4 w-4 mr-2" />
                Purchase Credits
              </Button> */}
              
              <p className="text-xs text-gray-500">
                Credits are added instantly after purchase
              </p>
            </div>
          )}
        </CardContent>
      </Card>
    );
  }

  return children;
};

export default CreditGuard;
